  width: 100%;
  height: 309px;
  max-width: unset;
  object-fit: cover;
}

.backdrop {
  width: 100vw;
  height: 100%;
  opacity: 1;
  background-color: rgba(0, 0, 0, .725);
  background-size: cover;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  display: none;
}

@keyframes spinner-line-fade-more {
  0%, 100% {
    opacity: 0;
  }

  1% {
    opacity: 1;
  }
}

@keyframes spinner-line-fade-quick {
  0%, 39%, 100% {
    opacity: .25;
  }

  40% {
    opacity: 1;
  }
}

@keyframes spinner-line-fade-default {
  0%, 100% {
    opacity: .22;
  }

  1% {
    opacity: 1;
  }
}

@keyframes spinner-line-shrink {
  0%, 25%, 100% {
    opacity: .25;
    transform: scale(.5);
  }

  26% {
    opacity: 1;
    transform: scale(1);
  }
}

